/* open-sans-regular - cyrillic */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  display: swap;
  font-weight: 400;
  src: local(""), url("../fonts/open-sans-v23-cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v23-cyrillic-regular.woff") format("woff"); }

/* open-sans-600 - cyrillic */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  display: swap;
  font-weight: 600;
  src: local(""), url("../fonts/open-sans-v23-cyrillic-600.woff2") format("woff2"), url("../fonts/open-sans-v23-cyrillic-600.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none; }

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0; }

img {
  display: block;
  max-width: 100%;
  padding: 0;
  margin: 0; }

input,
button,
textarea,
select {
  font: inherit; }

.page {
  background-color: #e5e5e5; }

.page-body {
  font-family: 'Open Sans', sans-serif;
  max-width: 1440px;
  height: 100%;
  margin: 0 auto;
  background-color: #ffffff; }

.page-header {
  padding-top: 21px;
  background-color: rgba(0, 0, 0, 0.5);
  padding-bottom: 240px; }

.page-header__wrap {
  display: grid;
  grid-template-columns: 393px 1fr;
  -webkit-column-gap: 160px;
     -moz-column-gap: 160px;
          column-gap: 160px;
  margin: 256px 280px 0; }

.main-nav {
  max-width: 873px;
  margin: 0 auto; }

.main-nav__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.benefits {
  padding: 150px 137px 130px;
  text-align: center; }

.benefits__wrap {
  margin-top: 102px; }

.benefits__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  -webkit-column-gap: 38px;
     -moz-column-gap: 38px;
          column-gap: 38px; }

.types {
  padding: 149px 157px 147px; }

.types__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.types__item:first-child {
  margin-top: 10px; }

.types__item:nth-child(2) {
  margin-top: 39px; }
